.search-input__wrapper {
  background: var(--bs-gray);
  border: 1px solid var(--bs-primary);
  transition: all 0.3s ease;
  width: 39%;
}
.search-input__wrapper.white {
  background: var(--bs-white);
  color: var(--bs-primary);
}
.search-input__wrapper svg {
  width: 21px;
  height: 21px;
  transition: all 0.3s ease;
}
.search-input__wrapper svg > use {
  stroke: var(--bs-primary);
  fill: transparent;
}
.search-input__wrapper input {
  transition: all 0.3s ease;
  background: transparent;
  color: var(--bs-primary);
}
.search-input__wrapper input::placeholder {
  color: var(--bs-primary);
}
.search-input__wrapper:not(.white):hover {
  background: var(--bs-blue);
}
.search-input__wrapper:not(.white):hover input {
  background: transparent;
}
.search-input__wrapper.focus svg {
  width: 0;
  opacity: 0;
  visibility: hidden;
  margin-right: 0 !important;
}

.search-input {
  border: none;
  background: var(--bs-gray);
  outline: none;
  flex: 1 1 auto;
}
.search-input::placeholder {
  color: var(--transparent-accordion-color);
}
.search-input.white {
  color: var(--bs-primary);
}
.search-input.white::placeholder {
  color: var(--bs-primary);
}

.map-address__btn {
  color: var(--dark-blue-color);
  border: 1px solid var(--dark-blue-color);
}
.map-address__btn:hover {
  text-decoration: underline;
  color: var(--dark-blue-color);
}